<template>
  <div class="single-device">
    <tab :data="tabList" @change="changeTab" :current="tabIndex"></tab>
    <div class="content">
      <transition :enter-active-class="routerTransitionClass.enter"
                      :leave-active-class="routerTransitionClass.leave">
          <component :is="is"></component>
      </transition>
    </div>
  </div>
</template>

<script>
import tab from '@/components/common-tab'
import singleDevice from './components/single-device.vue'
import detailTarget from './components/detail-target.vue'
import RouterTransition from '@/mixins/router-transition'
import orignalData from './components/orignal-data.vue'
import twinApplication from './components/twin-application.vue'
export default {
  name: 'platform',
  components: {
    tab,
    singleDevice,
    detailTarget,
    orignalData,
    twinApplication
  },
  mixins: [RouterTransition],
  data () {
    return {
      tabList: [
        {
          id: 1,
          name: '',
          com: singleDevice
        },
        {
          id: 2,
          name: '',
          com: detailTarget
        },
        {
          id: 3,
          name: '',
          com: orignalData
        },
        {
          id: 4,
          name: '',
          com: twinApplication
        }
      ],
      tabIndex: 0,
      is: null
    }
  },
  created () {
    this.is = this.tabList[this.tabIndex].com
  },
  methods: {
    changeTab (index) {
      console.log(index)
      this.tabIndex = index
      this.is = this.tabList[index].com
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient';
@size-padding: 10px;
.single-device{
  position: relative;
  width: 100%;
  height: 100%;
  .content{
    width: 100%;
    height: calc(~"100% - " 40px);
    // background-color: #fff;
  }
}
</style>



