<template>
    <div class="common-box">
      <div class="left-control">
        <div class="unit">{{info.name}}</div>
        <Select @on-change="viewChange($event,1)" style="width:100px;margin-bottom:6px;font-size:12px" v-model="search.deviceUnit">
          <Option v-for="item in type" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Select @on-change="viewChange($event,2)" style="width:100px;margin-bottom:6px" v-model="search.dimension">
          <Option v-for="item in views" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Input placeholder="" @on-enter="viewChange($event,3)" @on-click="viewChange($event,3)" style="width:100px" v-model="search.name"></Input>
      </div>
      <div class="loading" v-if="!autoplay">
        <Icon type="ios-loading" style="animation: ani-demo-spin 1s linear infinite;"/>
      </div>
      <Carousel :autoplay="autoplay" dots="none" :value="carouselValue" class="moveBox" @on-change ="changeCarousel" :autoplay-speed="speed">
        <CarouselItem   v-for="(item,index) in newArr" :key="index"  style="display:flex">
          <div @drag="drag" @dragend="dragend" class="droppable-element" draggable="true"
            unselectable="on"
            v-for="(o,index) in item" :key="index"
            :id="o.indexId" >
            <single-chart
              :list="o.datas"
              :selfId="o.indexId"
              :info="o"
              @init="init"
              ref="chart"
              ></single-chart>
          </div>
        </CarouselItem>
      </Carousel>
    </div>
</template>
<script>
import customChart from '@/components/common-chart/common-chart-little'
import singleChart from './single-chart.vue'
import { getChart, getIndex } from '@/api/device'
export default {
  name: 'detail-target',
  components: {
    customChart,
    singleChart
  },
  props: {
    info: {},
    type: {
      type: Array
    }
  },
  data () {
    return {
      carouselValue: 0,
      autoplay: false,
      search: {
        dimension: 'min',
        deviceUnit: '',
        name: ''
      },
      speed: 10000,
      tab: 1,
      list: [],
      views: [
        {name: '100ms', id: 'mill'},
        {name: '1min', id: 'min'},
        {name: '1h', id: 'hour'}
      ],
      newArr: [],
      timer: {},
      ids: []
    }
  },
  mounted () {
    this.getChartIds()
  },
  watch: {
    list (val) {
      if (val.length <= 3 && this.timer === {}) {
        this.timer = setInterval(() => {
          this.newArr = []
          this.getCharts(this.ids, 1)
        }, 10000)
      }
    }
  },
  destroyed () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  },
  methods: {
    changeCarousel (oldValue, value) {
      if (this.newArr[value].length < 3) {
        this.carouselValue = 0
      }
      if (value % 2 !== 0) {
        if (!this.ids[value * 6 + 6] && this.ids[value * 6]) {
          this.getCharts(this.ids.slice(value * 6), 2)
        } else if (!this.ids[value * 6 + 6] && !this.ids[value * 6]) {
          this.newArr = []
          this.getCharts(this.ids.slice(0, 6), 1)
        } else {
          this.getCharts(this.ids.slice(value * 6, value * 6 + 6), 2)
        }
      }
    },
    getChartIds () {
      this.autoplay = false
      let {name: indexName, deviceUnit} = this.search
      getIndex({pointType: this.info.type, indexName, deviceUnit}).then(res => {
        let {code, data} = res
        if (code === 200) {
          if (data[0] && data[0].pointTypeList) {
            let list = data[0].pointTypeList
            let ids = []
            for (let item of list) {
              ids.push(item.id)
            }
            this.ids = ids
            this.newArr = []
            this.getCharts(ids.slice(0, 6), 1)
            if (indexName || deviceUnit) {
              this.carouselValue = 0
            }
          }
        }
      })
    },
    getCharts (ids, type) {
      let idsList = ids.join(',')
      getChart({indexId: idsList, timeType: this.search.dimension}).then(res => {
        let {code, data} = res
        if (code === 200) {
          this.list = data.map(i => {
            return {
              ...i,
              datas: i.datas ? i.datas.map((o, index) => {
                return {
                  value: Number(o.value).toFixed(2),
                  timestamp: this.changeTimestamp(this.search.dimension, o.timestamp, index)
                }
              }) : []
            }
          })
          let index = 0
          for (let i = 0; i < this.list.length; i++) {
            if (i % 3 === 0 && i !== 0) { //  3 
              if (this.list.slice(index, i).length !== 0) {
                this.newArr.push(this.list.slice(index, i))
                index = i
              }
            };
            if ((i + 1) === this.list.length) {
              if (this.list.slice(index, (i + 1)).length !== 0) {
                this.newArr.push(this.list.slice(index, (i + 1)))
              }
            }
          };

          this.$nextTick(() => {
            if (this.$refs.chart) {
              for (let item of this.$refs.chart) {
                item.getInfo()
              }
              this.autoplay = true
              if (type === 1) {
                this.carouselValue = 0
              }
            }
          })
        }
      })
    },
    init (e) {
      this.autoplay = e
    },
    changeTimestamp (type, timestamp, index) {
      if (timestamp) {
        if (Number(index) === 0) {
          switch (type) {
            case 'min':
              return timestamp.slice(8, 10) + ':' + timestamp.slice(10, 12)
            case 'hour':
              return timestamp.slice(8, 10)
            case 'mill':
              return timestamp.slice(10, 12) + ':' + timestamp.slice(12, 14) + ':' + timestamp.slice(-3)
          }
        }
        switch (type) {
          case 'min':
            return timestamp.slice(10, 12)
          case 'hour':
            return timestamp.slice(8, 10)
          case 'mill':
            return timestamp.slice(-3)
        }
      } else {
        return '100'
      }
    },
    drag: function (e) {},
    dragend: function (e) {
      console.log(e)
      this.$emit('dragInfo', e.target.id)
    },
    viewChange (e, type) {
      clearInterval(this.timer)
      this.timer = {}
      this.getChartIds()
    }
  }
}
</script>

<style lang="less" scoped>
.droppable-element{
  width: fit-content;
  // display: flex;
}
/deep/.ivu-carousel-arrow{
  top: 50% !important;
}
.common-box{
  display: flex;
  align-items: center;
  padding: 6px 12px 0px 9px;
  background-color: #19181D;
  width: 805px;
  height: 187px;
  margin-top: 24px;
  margin-right: 12px;
  position: relative;
    .loading{
      position: absolute;
      left: 117px;
      width: 686px;
      height: 180px;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 999;
      background-color: #19181D;
    }
    .moveBox{
      min-width: 686px;
    }
    .left-control{
    display: flex;
    flex-direction: column;
    align-items: center;
    .unit{
      font-size: 14px;
      font-family: Alibaba PuHuiTi;
      font-weight: 400;
      color: #FFFFFF;
      margin-bottom: 10px;
    }
  }
  /deep/.ivu-select-item{
    font-size: 12px !important;
  }
}
</style>



