<template>
  <div class="card">
    <div class="title-line">
      <div class="title">{{infoTemp.deviceUnitName}}</div>
      <!-- <Button v-if="!infoTemp.btn" @click="editChild" class="btn-edit"></Button> -->
      <Button v-if="infoTemp.btn" @click="deleteChild" class="btn-delete"></Button>
    </div>
    <div class="chart-container">
      <div class="title">{{infoTemp.pointName}}</div>
      <div class="lastInfo">{{getSimple()}}</div>
      <custom-chart :data="list"
                  v-bind="chartInfo"
                  :id="selfId"
                  :height="134"
                  ref="chart"
                  @init="init"
                  ></custom-chart>
    </div>
  </div>
</template>

<script>
import customChart from '@/components/common-chart/common-chart-little'
export default {
  name: 'detail-target',
  components: {
    customChart
  },
  props: {
    selfId: {
      type: Number
    },
    showBtn: {
      default: false
    },
    info: {
      type: Object
    },
    list: {
      type: Array
    }
  },
  computed: {
    infoTemp () {
      return this.info
    }
  },
  data () {
    return {
      chartInfo: {
        device: this.info.deviceUnitName,
        title: this.info.pointName,
        btn: this.info.btn,
        type: 'line',
        xAxis: {
          key: 'timestamp'
        },
        yAxis: {
          symbol: this.info.unitName
        },
        legend: [
          { key: 'value', color: '#0084FF' }
        ],
        grid: {
          top: '15',
          bottom: '0',
          left: '15',
          right: '100'
        }
      }
    }
  },
  mounted () {
    this.getInfo()
  },
  methods: {
    deleteChild () {
      this.$emit('deleteItem', this.selfId)
    },
    editChild () {

    },
    getSimple () {
      let datas = this.list[this.list.length > 0 ? this.list.length - 1 : 0] && this.list[this.list.length > 0 ? this.list.length - 1 : 0].value
      if (datas) {
        return Number(datas).toFixed(2)
      } else {
        return '0.00'
      }
    },
    init (e) {
      this.$emit('init', e)
    },
    getInfo () {
      let {deviceUnitName, unitName, pointName} = this.info
      this.chartInfo = {
        device: deviceUnitName,
        title: pointName,
        type: 'line',
        xAxis: {
          key: 'timestamp'
        },
        yAxis: {
          symbol: unitName
        },
        legend: [
          { key: 'value', color: '#0084FF' }
        ],
        grid: {
          top: '15',
          bottom: '0',
          left: '15',
          right: '100'
        }
      }
      this.$refs.chart && this.$refs.chart.init()
    }
  }
}
</script>

<style lang="less" scoped>
.card {
  width: fit-content;
  position: relative;
  margin-left: 12px;
  .title-line{
    width:fit-content;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 7px;
    .title {
      margin-left: 8px;
      font-size: 18px;
      font-family: Alibaba PuHuiTi;
      font-weight: 400;
      color: #FFFFFF;
      width: 150px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
    }
    .btn-delete{
      background: #FF1010;
      color: #fff;
    }
    .btn-edit{
      background: #007ACC;
      color: #fff;
    }
  }

  .chart-container {
    width: 216px;
    height: 136px;
    background-color: #222126;
    position: relative;
    .lastInfo{
      position: absolute;
      top: 7px;
      right: 14px;
      width: 30px;
      font-size: 16px;
      font-family: Alibaba PuHuiTi;
      font-weight: 800;
      color: #0084FF;
    }
    .title{
      position: absolute;
      top: 7px;
      left: 7px;
      width: 150px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
    }
  }
}
</style>



