<template>
  <div class="single-device">
    <div class="left">
      <div class="left-head border"></div>
      <div class="left-device border">
        <div class="form">
          <div class="form-item form-item-select">
            <div class="label"></div>
            <div class="select">
              <Select v-model="form.place" @on-change="changeGroup">
                <Option
                  v-for="item in placeOptions"
                  :key="item.id"
                  :value="item.deviceId"
                >
                  {{ item.name }}
                </Option>
              </Select>
            </div>
          </div>
          <div class="form-item form-item-select">
            <div class="label"></div>
            <div class="select">
              <Select v-model="form.name" @on-change="changeSingle">
                <Option
                  v-for="item in nameOptions"
                  :key="item.id"
                  :value="item.deviceId"
                >
                  {{ item.name }}
                </Option>
              </Select>
            </div>
          </div>
          <div class="form-item form-item-button">
            <Button type="default" @click="showDrawer"></Button>
          </div>
        </div>
      </div>
      <div class="left-enivronment border">
        <!-- <div class="title" >(++PM2.5)</div> -->
        <div class="title" @mouseenter="ChangeShow(1)" v-if="showTitle">
          {{ getName(chartId) }}
        </div>
        <div class="title" @mouseleave="ChangeShow(2)" v-if="!showTitle">
          <Select v-model="chartId" @on-change="changeChart">
            <Option
              v-for="item in chartOptions"
              :key="item.id"
              :value="item.id"
            >
              {{ item.name }}
            </Option>
          </Select>
        </div>
        <div class="chart" id="chart" style="width: 100%;height:200px;"></div>
      </div>
      <div class="left-danger">
        <div class="title"></div>
        <div class="left-dangertable">
          <Table
            class="m-table no-scroll no-border"
            stripe
            :loading="loading.dangerTable"
            :columns="dangerTableColumns"
            highlight-row
            height="400%"
            :data="dangerTableData"
          ></Table>
        </div>
      </div>
    </div>
    <div class="right">
      <div :class="['right-monitor', isFull ? 'active' : '']">
        <div class="right-head">
          <div class="right-head-left"></div>
          <div class="right-head-right">
            <div style="margin-right:10px"></div>
            <div class="select" style="width:100px">
              <Select v-model="time" @on-change="freshTimeChange">
                <Option
                  v-for="item in freshTime"
                  :key="item.id"
                  :value="item.value"
                >
                  {{ item.value }}
                </Option>
              </Select>
            </div>
            <div class="select">
              <Select v-model="screen" @on-change="screenChange">
                <Option
                  v-for="item in screenOptions"
                  :key="item.id"
                  :value="item.id"
                >
                  {{ item.name }}
                </Option>
              </Select>
            </div>
            <div class="jump">
              <Button
                :type="screen === '1' || screen === '4' ? 'primary' : 'default'"
                @click="jump"
                :disabled="screen === '1' || screen === '4'"
                custom-icon="iconfont icon-tiaozhuanmianban"
                ></Button
              >
            </div>
            <div class="jump">
              <Button type="default" @click="navToEdit()"></Button>
            </div>
            <div class="full-screen">
              <Button
                @click="fullSreen"
                custom-icon="iconfont icon-tiaozhuanmianban"
                >{{ isFull ? "" : "" }}</Button
              >
            </div>
          </div>
        </div>
        <div class="right-screen">
          <GraphViewer
            ref="graphViewer"
            :module-id="moduleId"
            @initViewer="initViewer()"
          ></GraphViewer>
        </div>
      </div>
      <div class="right-target">
        <div class="right-target-item right-target-financial">
          <div class="label"></div>
          <div class="content">
            <div
              class="content-item"
              v-for="item in financialList"
              :key="item.id"
            >
              <div>{{ item.name }}</div>
              <div>
                <span class="num">{{ item.num }}</span>
                <span class="symbol">/{{ item.symbol }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="right-target-item right-target-performence">
          <div class="label"></div>
          <div class="content">
            <div
              class="content-item"
              v-for="item in performenceList"
              :key="item.id"
            >
              <div class="icon" :style="{ backgroundColor: item.bg }">
                <span
                  :class="['iconfont', item.icon]"
                  :style="{ color: item.color }"
                ></span>
              </div>
              <div class="data">
                <div>{{ item.name }}</div>
                <div>
                  <span class="num">{{ item.num }}</span>
                  <span class="symbol">/%</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="right-target-item right-target-reliable">
          <div class="label"></div>
          <div class="content">
            <div
              class="content-item"
              v-for="item in reliableList"
              :key="item.id"
            >
              <div class="icon" :style="{ backgroundColor: item.bg }">
                <span
                  :class="['iconfont', item.icon]"
                  :style="{ color: item.color }"
                ></span>
              </div>
              <div class="data">
                <div>{{ item.name }}</div>
                <div>
                  <span class="num">{{ item.num }}</span>
                  <span class="symbol">{{ item.symbol }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <custom-drawer
      ref="drawer"
      width="500"
      title=""
      :formValidate="formValidate"
      :formList="formList"
      v-model="formValue"
      @confirm="confirm"
    ></custom-drawer>
  </div>
</template>

<script>
import { Select } from 'iview'
import { getToken } from '@/libs/util'
import GraphViewer from '@/components/graph-viewer'
import {
  getlistDeviceGroup,
  exportData,
  getChartSingle,
  getRealTimeMonitor,
  getChartType,
  changeChartType,
  getDeviceInfo,
  getWarningInfo
} from '@/api/device'
import customDrawer from '@/components/common-drawer/common-drawer'
export default {
  name: 'single-device',
  components: {
    Select,
    GraphViewer,
    customDrawer
  },
  data () {
    return {
      freshTime: [
        { id: '1', value: '5s' },
        { id: '2', value: '10s' },
        { id: '3', value: '60s' }
      ],
      time: '5s',
      form: {
        name: '',
        place: ''
      },
      chartId: 'POWER_GENERATION_AMOUNT',
      formList: [
        {
          label: '',
          type: 'datePicker',
          value: 'date',
          range: 1
        }
      ],
      formValidate: {
        date: [{ required: true, message: '' }]
      },
      formValue: {},
      showTitle: true,
      token: getToken(),
      disabled: true,
      nameOptions: [],
      chartOptions: [
        {
          id: 'POWER_GENERATION_AMOUNT',
          name: ''
        },
        {
          id: 'CARBON_EMISSIONS',
          name: ''
        },
        {
          id: 'WATER_CONSUMPTION',
          name: ''
        },
        {
          id: 'GAS_CONSUMPTION',
          name: ''
        },
        {
          id: 'POWER_GENERATION_EFFICIENCY',
          name: ''
        },
        {
          id: 'POWER_GENERATION',
          name: ''
        },
        {
          id: 'RUN_TIME',
          name: ''
        },
        {
          id: 'FAIL_NUM',
          name: ''
        },
        {
          id: 'TROUBLE_FREE_OPERATION_TIME',
          name: ''
        },
        {
          id: 'DOWN_TIME',
          name: ''
        }
      ],
      placeOptions: [],
      screenOptions: [
        {
          id: '1',
          name: 'PI&D'
        },
        {
          id: '2',
          name: ''
        },
        {
          id: '3',
          name: ''
        },
        {
          id: '4',
          name: ''
        }
      ],
      isFull: false,
      deviceTableColumns: [
        {
          title: '',
          key: 'device',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '',
          key: 'subDevice',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '',
          key: 'person',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '',
          key: 'content',
          align: 'left',
          ellipsis: true,
          tooltip: true
        }
      ],
      dangerTableColumns: [
        {
          title: '',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '',
          key: 'errorDesc',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '',
          key: 'alertTime',
          align: 'left',
          ellipsis: true,
          tooltip: true
        }
      ],
      deviceTableData: [
        {
          device: '111',
          subDevice: '111-222',
          person: '',
          content: '12345667885'
        },
        {
          device: '111',
          subDevice: '111-222',
          person: '',
          content: '12345667885'
        },
        {
          device: '111',
          subDevice: '111-222',
          person: '',
          content: '12345667885'
        },
        {
          device: '111',
          subDevice: '111-222',
          person: '',
          content: '12345667885'
        },
        {
          device: '111',
          subDevice: '111-222',
          person: '',
          content: '12345667885'
        }
      ],
      dangerTableData: [],
      screen: '1',
      moduleId: '',
      loading: {
        deviceTable: false,
        dangerTable: false
      },
      deviceCurrentIndex: 0,
      dangerCurrentIndex: 0,
      financialList: [
        {
          id: 1,
          name: '',
          type: 'POWER_TOTAL',
          num: '130',
          symbol: 'kWh'
        },
        {
          id: 2,
          name: '',
          type: 'POWER_DAILY',
          num: '130',
          symbol: 'kWh'
        },
        {
          id: 3,
          name: '',
          type: 'ONLINE_POWER_TOTAL',
          num: '130',
          symbol: 'kWh'
        },
        {
          id: 4,
          name: '',
          type: 'CARBON_REDUCE_TOTAL',
          num: '130',
          symbol: 't'
        },
        {
          id: 5,
          name: '',
          type: 'WATER_CONSUMPTION_TOTAL',
          num: '130',
          symbol: 'kg'
        },
        {
          id: 6,
          name: '',
          type: 'GAS_CONSUMPTION_TOTAL',
          num: '130',
          symbol: 'm'
        }
      ],
      performenceList: [
        {
          id: 1,
          name: '',
          num: '96',
          icon: 'icon-zuigao',
          bg: 'rgb(23, 80, 236, .2)',
          type: 'HISTORY_MAX_POWER_EFFICIENCY',
          color: '#1750EC'
        },
        {
          id: 2,
          name: '',
          num: '96',
          icon: 'icon-zuida',
          bg: 'rgba(0, 132, 255, .2)',
          type: 'HISTORY_MAX_POWER_EFFICIENCY',
          color: '#0084FF'
        },
        {
          id: 3,
          name: '',
          num: '96',
          icon: 'icon-pingjun',
          bg: 'rgba(36, 204, 53, .2)',
          type: 'HISTORY_MAX_AVG_POWER_EFFICIENCY',
          color: '#24CC35'
        }
      ],
      reliableList: [
        {
          id: 1,
          name: '',
          num: '96',
          symbol: '/h',
          icon: 'icon-a-guzhang2',
          bg: 'rgba(241, 33, 33, .2)',
          type: 'RUNTIME_TOTAL',
          color: '#F12121'
        },
        {
          id: 2,
          name: '',
          num: '96',
          symbol: '/',
          icon: 'icon-a-lianhe62',
          bg: 'rgba(250, 197, 22, .2)',
          type: 'DOWN_NUM_AVG_DAILY',
          color: '#FAC516'
        },
        {
          id: 3,
          name: '',
          num: '96',
          symbol: '/',
          icon: 'icon-shichang',
          bg: 'rgba(255, 118, 0, .2)',
          type: 'FAIL_NUM_TOTAL',
          color: '#FF7600'
        }
      ],
      deviceId: '',
      parentId: ''
    }
  },
  computed: {
    configurationUrl () {
      let baseUrl = window.location.href.split('device')[0]
      let extra = 'mxgraph/#/home'
      return baseUrl + extra
    }
  },
  async mounted () {
    await this.getDeviceGroup()
    this.getDeviceSingle(this.parentId)
    this.getChartType()
    this.getDeviceInfo()
  },
  destroyed () {
    localStorage.removeItem('freshTime')
  },
  methods: {
    getDeviceInfo () {
      getDeviceInfo({ id: this.deviceId }).then(res => {
        if (res.code === 200) {
          let info = res.data
          for (let item of this.financialList) {
            item.num = info.economyIndex[item.type]
          }
          for (let item of this.performenceList) {
            item.num = info.performanceIndex[item.type]
          }
          for (let item of this.reliableList) {
            item.num = info.reliabilityIndex[item.type]
          }
        }
      })
      this.loading.dangerTable = true
      getWarningInfo({
        deviceId: this.deviceId,
        pageNumber: 1,
        pageSize: 10
      }).then(res => {
        if (res.code === 200) {
          this.dangerTableData = res.data.result
          this.loading.dangerTable = false
        }
      })
    },
    getChartType () {
      getChartType({ id: this.deviceId }).then(res => {
        if (res.code === 200) {
          if (res.data.sceneType) {
            this.chartId = res.data.sceneType
          } else {
            this.chartId = 'POWER_GENERATION_AMOUNT'
          }
          this.getChartData()
        }
      })
    },
    screenChange (e) {
      this.screen = e
      if (Number(e) === 4) {
        getRealTimeMonitor({ id: this.deviceId }).then(res => {
          if (res.code === 200) {
            this.moduleId = res.data.id
            this.$refs.graphViewer.loadModuleInfo(res.data.id)
          }
        })
      } else {
        for (let item of this.nameOptions) {
          if (item.deviceId === this.deviceId) {
            this.moduleId = item.id
            this.$refs.graphViewer.loadModuleInfo(item.id)
          }
        }
      }
    },
    changeChart (e) {
      changeChartType({ deviceId: this.deviceId, sceneType: e }).then(res => {
        if (res.code === 200) {
          console.log('')
        }
      })
      this.chartId = e
      this.getChartData()
    },
    freshTimeChange (e) {
      localStorage.setItem('freshTime', e.slice(0, -1))
    },
    getName (id) {
      for (let item of this.chartOptions) {
        if (id === item.id) {
          return item.name
        }
      }
    },
    changeGroup (e) {
      this.getDeviceSingle(e)
      this.getChartType()
    },
    changeSingle (e) {
      for (let item of this.nameOptions) {
        if (Number(this.form.name) === Number(item.deviceId)) {
          this.deviceId = item.deviceId
          this.moduleId = item.id
        }
      }
      this.screen = '1'
      this.getDeviceInfo()
      this.$forceUpdate()
      this.initViewer()
      this.getChartType()
    },
    confirm (e) {
      let baseUrl = window.location.origin
      if (e) {
        let info = {
          startTime: this.getRegularTime(this.formValue.date[0]),
          endTime: this.getRegularTime(this.formValue.date[1]),
          deviceId: this.deviceId
        }
        exportData(info).then(res => {
          if (res.code === 200) {
            this.$Message.success('')
            let path = '/device-batch-ai/download/'
            window.open(baseUrl + path + encodeURIComponent(res.data.fileName))
            this.$refs.drawer.close()
          } else {
            this.$Message.warning(res.msg)
          }
        })
      }
    },
    getRegularTime (time) {
      let timeformat = time
      let year = timeformat.getFullYear().toString()
      let month = this.getString(timeformat.getMonth() + 1)
      let day = this.getString(timeformat.getDate())
      let hour = this.getString(timeformat.getHours())
      let min = this.getString(timeformat.getMinutes())
      let sec = this.getString(timeformat.getSeconds())
      let nowTime =
        year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec
      return nowTime
    },
    getString (val) {
      return val < 10 ? '0' + val.toString() : val.toString()
    },
    showDrawer () {
      this.$refs.drawer.open()
    },
    ChangeShow (type) {
      if (type === 1) {
        this.showTitle = false
      } else {
        setTimeout(() => {
          this.showTitle = true
        }, 2000)
      }
    },
    getDeviceSingle (parentId) {
      let { deviceId } = this.$route.query
      for (let item of this.placeOptions) {
        if (item.deviceId == parentId) {
          if (item.child) {
            this.nameOptions = item.child.map(i => {
              return {
                name: i.unitName,
                id: i.mxgraphId,
                deviceId: i.deviceId
              }
            })
            for (let oitem of this.nameOptions) {
              if (Number(oitem.deviceId) === Number(deviceId)) {
                this.form.name = oitem.deviceId
                this.moduleId = oitem.id
                this.deviceId = oitem.deviceId
              }
            }
            if (this.form.name === '') {
              this.form.name = this.nameOptions[0].deviceId || ''
              this.moduleId = this.nameOptions[0].id || ''
              console.log(this.moduleId)
              this.deviceId = this.nameOptions[0].deviceId || ''
            }
          } else {
            this.nameOptions = []
            this.form.name = ''
            this.moduleId = ''
            this.deviceId = ''
          }
        }
      }
    },
    getDeviceGroup () {
      return new Promise((resolve, reject) => {
        let { parentId } = this.$route.query
        getlistDeviceGroup().then(res => {
          let { code, data } = res
          if (code === 200) {
            this.placeOptions = data.map(i => {
              return {
                name: i.deviceName,
                id: i.mxgraphId,
                deviceId: i.deviceId,
                downLoadId: i.id,
                child: i.child
              }
            })
            for (let item of this.placeOptions) {
              if (Number(item.deviceId) === Number(parentId)) {
                this.form.place = item.deviceId
                this.parentId = item.deviceId
              }
            }
            if (this.form.place === '') {
              this.form.place = this.placeOptions[0].deviceId
              this.parentId = this.placeOptions[0].deviceId
            }
            resolve(res)
          }
        })
      })
    },
    navToEdit () {
      const id = `?moduleId=${this.moduleId}`
      const token = `&token=${this.token}`
      let deviceId = `&deviceId=`
      if (this.screen !== '4') {
        for (let item of this.nameOptions) {
          if (item.id === this.moduleId) {
            deviceId = deviceId + item.deviceId
          }
        }
      } else {
        deviceId = deviceId + this.deviceId
      }
      window.open(this.configurationUrl + id + token + deviceId, '_blank')
    },
    getChartData () {
      let { deviceId, chartId: businessType } = this
      getChartSingle({ deviceId, businessType }).then(res => {
        if (res.code === 200) {
          let data = res.data.indexs[0]
          let xData = data.value.map(i => {
            return i.x
          })
          let legend = data.legend.map(i => {
            return i.name
          })
          let yDatas = []
          for (let item of data.legend) {
            let array = []
            for (let datas of data.value) {
              array.push(datas[item.key])
            }
            yDatas.push(array)
          }
          let series = []
          for (let idx = 0; idx <= yDatas.length - 1; idx++) {
            let colors = this.getColors(idx)
            series.push({
              name: legend[idx],
              data: yDatas[idx],
              type: 'line',
              smooth: true,
              itemStyle: {
                color: colors.color
              },
              lineStyle: {
                color: colors.color,
                shadowBlur: 6,
                shadowColor: colors.shadowColor,
                shadowOffsetY: 3
              },
              emphasis: {
                label: {
                  show: true,
                  position: 'outside'
                }
              }
            })
          }
          let info = { series, xData, legend }
          this.initChart(info)
        } else {
          let info = { series: [], xData: [], legend: [] }
          this.initChart(info)
        }
      })
    },
    getColors (index) {
      switch (index) {
        case 0:
          return { color: '#1CC96C', shadowColor: 'rgba(255, 255, 255, 0.6)' }
        case 1:
          return { color: '#FFCE08', shadowColor: 'rgba(184, 193, 29, 1)' }
        case 2:
          return { color: '#0084FF', shadowColor: 'rgba(0, 0, 0, 0.16)' }
        case 3:
          return { color: '#ffb980', shadowColor: 'rgba(0, 0, 0, 0.66)' }
      }
    },
    initChart (info) {
      console.log('')
      let chart = document.getElementById('chart')
      this.$echarts.dispose(chart)
      let line = this.$echarts.init(chart)
      line.setOption({
        xAxis: {
          type: 'category',
          data: info.xData,
          axisLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.6)'
            }
          },
          axisLabel: {
            color: 'rgba(255, 255, 255, 0.6)'
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          type: 'value',
          scale: true,
          splitNumber: 5,
          axisLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.6)'
            }
          },
          axisLabel: {
            color: 'rgba(255, 255, 255, 0.6)'
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              type: 'dashed',
              color: 'rgba(255, 255, 255, 0.6)'
            }
          }
        },
        grid: {
          bottom: 20,
          left: 60,
          right: 0
        },
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        legend: {
          data: info.legend,
          icon: 'circle',
          itemWidth: 7,
          itemHeight: 7,
          right: 0,
          textStyle: {
            color: '#cccc'
          }
        },
        series: info.series
      })
    },
    jump () {
      if (this.screen === '2') {
        this.$router.push({
          name: 'health-check-list'
        })
      } else if (this.screen === '3') {
        this.$parent.changeTab(3)
      }
    },
    fullSreen () {
      // let el = document.documentElement
      // let rfs = el.requestFullscreen || el.webkitRequestFullScreen || el.mozRequestFullScreen || el.msRequestFullScreen
      // if (!this.isFull) {
      //   rfs.call(el)
      // } else {
      //   if (document.exitFullscreen) {
      //     document.exitFullscreen()
      //   } else if (document.webkitCancelFullScreen) {
      //     document.webkitCancelFullScreen()
      //   } else if (document.mozCancelFullScreen) {
      //     document.mozCancelFullScreen()
      //   } else if (document.msExitFullscreen) {
      //     document.msExitFullscreen()
      //   }
      // }
      this.isFull = !this.isFull
      this.initViewer()
    },
    initViewer () {
      this.$refs.graphViewer.reset()
    },
    handleClick (row) {
      console.log(row)
    }
  }
}
</script>

<style lang="less" scoped>

.single-device {
  display: flex;
  width: 100%;
  height: 100%;
  .border {
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  }
  .left {
    width: 465px;
    padding: 0 24px;
    // background-color: #fff;
    background-color: #262c3a;
    border-right: 1px solid rgba(0, 0, 0, 0.08);
    overflow: scroll;
    &-head {
      margin-top: 24px;
      padding-bottom: 12px;
      font-size: 16px;
    }
    &-device {
      padding: 24px 0;
      .form {
        width: 100%;
        display: flex;
        align-items: flex-end;
        margin-bottom: 12px;
        &-item-select {
          width: 160px;
          margin-right: 12px;
          font-size: 14px;
          // color: #000000;
          color: #fff;
          .label {
            margin-bottom: 12px;
          }
          .select {
            height: 28px;
          }
        }
        &-item-button {
          flex: 1;
          margin-left: 12px;
          /deep/ .ivu-btn {
            width: 60px;
            height: 28px;
            background-color: #0084ff;
            border: none;
            color: #fff;
          }
        }
      }
    }
    &-enivronment,
    &-danger {
      padding: 24px 0;
      font-size: 14px;
      // color: #000000;
      color: #fff;
    }
    &-enivronment {
      .chart {
        margin-top: 12px;
      }
    }
    &-danger {
      .title {
        margin-bottom: 12px;
        color: #fff;
      }
    }
  }
  .right {
    width: 1199px;
    height: 100%;
    padding: 0 23px;
    overflow: auto;
    &-monitor {
      .right-head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 24px;
        // color: #000000;
        color: #fff;
        font-size: 16px;
        &-right {
          display: flex;
          align-items: center;
          .select,
          .jump {
            margin-right: 12px;
          }
          .select {
            width: 160px;
            height: 28px;
            font-size: 14px;
          }
          .jump,
          .full-screen {
            .ivu-btn {
              width: 80px;
              height: 28px;
              background: #000;
              border: none;
              color: rgba(255, 255, 255, 0.8);
            }
          }
          .jump {
            .ivu-btn-default {
              background-color: #0084ff;
              border: none;
              color: #fff;
            }
          }
        }
      }
      .right-screen {
        height: 627px;
        margin-top: 12px;
        margin-bottom: 24px;
        background-color: #f0f3f8;
      }
      &.active {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0 24px;
        // background-color: #fff;
        background-color: #262c3a;
        z-index: 999;
        .right-head {
          .ivu-btn {
            padding: 0 50px;
          }
        }
        .right-screen {
          width: 100%;
          height: calc(100% - 10px);
        }
      }
    }
    &-target {
      display: flex;
      align-items: center;
      margin-bottom: 11px;
      &-item {
        flex: 1;
        margin-right: 12px;
        .label {
          font-size: 14px;
          // color: #000000;
          color: #fff;
          margin-bottom: 12px;
        }
        .content {
          display: grid;
          gap: 8px;
          height: 180px;
          padding: 12px;
          border-radius: 8px;
          // background-color: #f0f3f8;
          background-color: #000;
          &-item {
            display: flex;
            border-radius: 8px;
            // background-color: #fff;
            background-color: #262c3a;
            font-size: 14px;
            text-align: center;
            box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.04);
            color: rgba(255, 255, 255, 0.6);
            .num {
              // color: #000000;
              color: #fff;
              // font-weight: 700;
              font-size: 18px;
            }
            .symbol {
              font-size: 12px;
            }
          }
        }
        &:last-child {
          margin-right: 0;
        }
      }
      &-financial {
        .content {
          grid-template-columns: repeat(3, 1fr);
          grid-template-rows: repeat(2, 1fr);
          &-item {
            flex-direction: column;
            justify-content: center;
            align-items: center;
          }
        }
      }
      &-performence,
      &-reliable {
        .content {
          grid-template-columns: repeat(2, 1fr);
          grid-template-rows: repeat(2, 1fr);
          &-item {
            align-items: center;
            .icon {
              width: 40px;
              height: 40px;
              margin: 0 12px 0 10px;
              border-radius: 12px;
              line-height: 40px;
              text-align: center;
              .iconfont {
                font-size: 24px;
              }
            }
          }
        }
      }
    }
  }
}
</style>



