<template>
  <div class="detail-target">
    <div class="card-group" >
      <div class="card"
      style="width:100%"
          v-for="(item, index) in chartInfo"
          :key="index">
        <div class="title">{{item.title}}</div>
        <div class="chart-container" style="height:100%;width:100%">
          <custom-chart :data="list[item.value]"
                        v-bind="item"
                        :id="Number(item.id)"
                        :height="height"
                        :showLegend="true"
                        @init="init"
                        ref="chart"
                        ></custom-chart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import customChart from '@/components/common-chart/common-chart'
export default {
  name: 'detail-target',
  components: {
    customChart
  },
  props: {
    chartList: {
      type: Array
    },
    data: {
      type: Object
    }
  },
  data () {
    return {
      temp: {},
      height: '300',
      width: '700'
    }
  },
  mounted () {
    if (this.$refs.chart) {
      if (this.$refs.chart.length === 1) {
        this.height = '580%'
      }
      if (this.$refs.chart.length <= 2) {
        this.width = '1500'
      }
      for (let item of this.$refs.chart) {
        item.init({height: this.height, width: this.width})
      }
    }
  },
  computed: {
    chartInfo: {
      get () {
        return JSON.parse(JSON.stringify(this.chartList))
      },
      set (val) {
        return val
      }
    },
    list: {
      get () {
        return JSON.parse(JSON.stringify(this.data))
      },
      set (val) {
        let chart = {}
        chart = JSON.parse(JSON.stringify(val))
        return chart
      }
    }
  },
  methods: {
    init (e) {
      let {chartList, data} = e
      this.chartInfo = JSON.parse(JSON.stringify(chartList))
      this.list = data
      if (this.$refs.chart) {
        for (let item of this.$refs.chart) {
          item.init()
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.detail-target {
  width: 100%;
  height: 100%;
  padding: 24px;
  overflow: hidden;
  .card-group{
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    .card {
      margin-right: 12px;
      margin-bottom: 24px;
      &:nth-child(3n) {
        margin-right: 0;
      }
      position: relative;
      .title {
        position: absolute;
        // margin-bottom: 12px;
        left: 25px;
        top: 30px;
        // color: rgba(0, 0, 0, 0.8);
        color: rgba(255, 255, 255, 0.8);
        font-size: 14px;
        font-family: Alibaba PuHuiTi;
        font-weight: 400;
      }
      .chart-container {
        padding: 11px 14px;
        background-color: #262C3a;
        border: 1px solid rgba(0, 0, 0, 0.06);
        border-radius: 8px;
      }
    }
  }
}
.btnGroup{
  display: flex;
  align-items: center;
  margin: 12px auto 24px;
  justify-content: center;
  div{
    margin-right: 12px;
    cursor: pointer;
    padding: 6px 12px;
    // border: 1px solid #000;
    border-radius: 4px;
  }
}
</style>



