/* eslint-disable */
<template>
  <div class="detail-target" :style="{overflow:fullScreen? 'hidden':'scroll'}">
    <div class="tab">
      <div class="btn" :style="{backgroundColor:tab === 1 ? '#0084FF':'#000'}" @click="changeTab(1)" ></div>
      <div class="btn" :style="{backgroundColor:tab === 2 ? '#0084FF':'#000'}" @click="changeTab(2)" ></div>
      <!-- <Button type="default" v-if="!fullScreen" class="download" @click="showDrawer"><span class="iconfont icon-tiaozhuanmianban" style="margin-right:14px;width:100px"></span></Button> -->
    </div>
    <div class="common-flex" :style="{height:fullScreen? '92%':'190px',marginBottom:fullScreen? '10px':'0'}">
      <div class="changeView" >
        <div style="margin-bottom:10px"></div>
        <Select @on-change="freeViewChange" style="width:100px;margin-bottom:12px" v-model="showFree">
          <Option v-for="item in views" :value="item.id" :key="item.id">{{ item.name }}</Option>
        </Select>
        <Button type="default" v-if="fullScreen" @click="changeScreen" style="width:100px"><span class="iconfont icon-tuichu"></span></Button>
      </div>
        <div class="grid" :style="{width:fullScreen ? '1494px':'1386px',height:fullScreen?'98%':'100%'}">
          <div :key="index" v-for="(item,index) in layout" v-show="!fullScreen">
            <single-chart
              :list="item.datas"
              :selfId="item.indexId + 10000000"
              :info="item"
              ref="chart"
              @deleteItem ="deleteItem"
            ></single-chart>
          </div>
          <compile-chart v-if="fullScreen" :data="infoData" :chartList="chartList" ref="complieChart" ></compile-chart>
        </div>
      <div class="changeBtn" v-if="!fullScreen">
        <Button type="default" style="margin-bottom:12px" @click="clearLayout"><span class="iconfont icon-qingkong" style="margin-right:14px;width:100px"></span></Button>
        <Button type="primary" v-if="!fullScreen" @click="changeScreen"><span class="iconfont icon-quanping" style="margin-right:14px;width:100px" ></span>{{fullScreen?'':''}}</Button>
      </div>
    </div>
    <div  class="flow" v-if="!fullScreen">
      <div v-for="(i,index) in typeList" :key="index">
        <chart-group @dragInfo="dragInfo" :info="i" :type="machineType"></chart-group>
      </div>
    </div>
     <!-- <custom-drawer ref="drawer"
        title=""
        :formValidate="formValidate"
        :formList="formList"
        v-model="formValue"
      @confirm="confirm"></custom-drawer> -->
  </div>
</template>

<script>
import customChart from '@/components/common-chart/common-chart-little'
import singleChart from './chart/single-chart.vue'
import compileChart from './chart/compile-chart.vue'
import chartGroup from './chart/chart-group.vue'
import { getMachineType, getChart } from '@/api/device'

export default {
  name: 'orignal-data',
  components: {
    customChart,
    singleChart,
    chartGroup,
    compileChart
  },
  data () {
    return {
      formList: [
        {
          label: '',
          type: 'select',
          value: 'type',
          data: [
            {
              value: '1',
              name: '1min'
            },
            {
              value: '2',
              name: '1hour'
            },
            {
              value: '3',
              name: '100ms'
            }
          ]
        },
        {
          label: '',
          type: 'datePicker',
          value: 'date',
          range: 1
        }
      ],
      formValidate: {
        type: [{ required: true, message: '' }],
        date: [{ required: true, message: '' }]
      },
      formValue: {},
      showFree: 'min',
      tab: 2,
      showList: [],
      layout: [],
      typeList: [
        {name: '', type: 'TEMPERATURE'},
        {name: 'V', type: 'VOLTAGE'},
        {name: 'mbar', type: 'PRESSURE'},
        {name: 'A', type: 'CURRENT'},
        {name: 'mg/s', type: 'FLOW'},
        {name: 'KW', type: 'POWER'}
      ],
      machineType: [],
      layoutStore: [],
      draggable: false,
      ids: [],
      sortArr: [],
      fullScreen: false,
      views: [
        {name: '100ms', id: 'mill'},
        {name: '1min', id: 'min'},
        {name: '1h', id: 'hour'}
      ],
      chartList: [],
      infoData: {}
    }
  },
  mounted () {
    this.getType()
  },
  methods: {
    confirm () {},
    getCharts (id) {
      return new Promise((resolve, reject) => {
        if (id) {
          this.ids.push(id)
        }
        this.layout = []
        this.layoutStore = []
        let list = this.ids.join(',')
        getChart({indexId: list, timeType: this.showFree}).then(res => {
          let {code, data} = res
          if (code === 200) {
            for (let item of data) {
              this.layout.push({
                ...item,
                btn: true,
                datas: item.datas.map((i, index) => {
                  return {
                    value: i.value,
                    timestamp: this.changeTimestamp(this.showFree, i.timestamp, index)
                  }
                }),
                'x': this.layout.length,
                'i': this.layout.length.toString()
              })
            }
            resolve(res)
          } else {
            reject(res)
          }
        })
      })
    },
    dragInfo (e) {
      if (this.layout.length === 6) {
        this.$Message.warning('')
      } else {
        if (!this.ids.includes(e)) {
          this.getCharts(e)
        } else {
          this.$Message.warning('')
        }
      }
    },
    deleteItem (e) {
      let chosenId = e - 10000000
      for (let i in this.layout) {
        let item = this.layout[i]
        if (item.indexId === chosenId) {
          this.layout.splice(Number(i), 1)
          this.layoutStore = this.clone(this.layout)
        }
      }
      for (let i in this.ids) {
        let item = this.ids[i]
        if (Number(item) === Number(chosenId)) {
          this.ids.splice(Number(i), 1)
        }
      }
    },
    changeTimestamp (type, timestamp, index) {
      if (timestamp) {
        if (Number(index) === 0) {
          switch (type) {
            case 'min':
              return timestamp.slice(8, 10) + ':' + timestamp.slice(10, 12)
            case 'hour':
              return timestamp.slice(8, 10)
            case 'mill':
              return timestamp.slice(10, 12) + ':' + timestamp.slice(12, 14) + ':' + timestamp.slice(-3)
          }
        }
        switch (type) {
          case 'min':
            return timestamp.slice(10, 12)
          case 'hour':
            return timestamp.slice(8, 10)
          case 'mill':
            return timestamp.slice(-3)
        }
      } else {
        return '100'
      }
    },
    getType () {
      getMachineType().then(res => {
        let {code, data} = res
        if (code === 200) {
          this.machineType = data.map(i => {
            return {
              name: i.unitName,
              id: i.id
            }
          })
        }
      })
    },
    freeViewChange () {
      if (this.ids.length > 0) {
        this.getCharts().then(() => {
          if (this.fullScreen) {
            this.compileChart()
          }
        })
      }
    },
    changeScreen (type) {
      if (this.layout.length === 0 && type !== 1) {
        this.$Message.warning('')
        return
      }
      // this.getCharts()
      this.layoutStore = JSON.parse(JSON.stringify(this.layout))
      if (this.fullScreen === false) {
        this.compileChart()
      } else {
        this.layout = this.layoutStore
        this.infoData = {}
        this.chartList = []
      }
      let domList = document.getElementsByClassName('detail-target')
      if (domList[0]) {
        domList[0].scrollTop = 0
      }
      this.fullScreen = type === 1 ? false : !this.fullScreen
    },
    compileChart () {
      let units = ['', 'V', 'mbar', 'A', 'mg/s', 'kw']
      let sortArr = []
      let range = []
      let data = {}
      let chartList = []
      // 
      for (let item of units) {
        for (let oitem of this.layout) {
          if (oitem.unitName === item) {
            sortArr.push(oitem)
          }
        }
      }
      // 
      for (let i in sortArr) {
        let item = sortArr[i]
        let nextItem = sortArr[Number(i) + 1]
        if (nextItem && item.unitName !== nextItem.unitName) {
          range.push(Number(i))
        }
      }
      if (!range.includes(5)) {
        range.splice(range.length, 0, 5)
      }
      for (let o in range) {
        let legend = []
        let title = ''
        let value = ''
        let symbol = ''
        let num = Number(o)
        let index = range[o]
        let preIndex = range[num > 0 ? num - 1 : 0]
        let idx = num === 0 ? 0 : preIndex + 1
        let limit = num === range.length - 1 ? sortArr.length - 1 : index
        let flag = 0
        let compileId = ''
        for (idx; idx <= limit; idx++) {
          let pitem = this.clone(sortArr[idx])
          title = this.getTitle(pitem.unitName)
          value = this.getValue(pitem.unitName)
          symbol = pitem.unitName
          let {color, key} = this.getColorAndKey(flag)
          let name = pitem.deviceUnitName + pitem.pointName
          legend.push({
            name, key, color
          })
          compileId = compileId + pitem.indexId
          for (let r in pitem.datas) {
            let dataItem = pitem.datas[r]
            if (!data[`${value}`]) {
              data[`${value}`] = []
            }
            if (!data[`${value}`][r]) {
              data[`${value}`][r] = {}
            }
            data[`${value}`][r].timestamp = dataItem.timestamp
            data[`${value}`][r][`${key}`] = dataItem.value
          }
          flag++
        }

        chartList.push({
          title,
          id: compileId,
          type: 'line',
          xAxis: {
            key: 'timestamp'
          },
          yAxis: {
            symbol
          },
          legend,
          value
        })
      }
      this.infoData = data
      this.chartList = chartList
      if (this.$refs.complieChart) {
        this.$nextTick(() => {
          this.$refs.complieChart.init({data, chartList})
        })
      }
    },
    clone (val) {
      return JSON.parse(JSON.stringify(val))
    },
    getColorAndKey (index) {
      switch (index) {
        case 0:
          return { color: '#0084FF', key: 'one' }
        case 1:
          return { color: '#1CC96C', key: 'two' }
        case 2:
          return { color: '#b6a2de', key: 'three' }
        case 3:
          return { color: '#ffb980', key: 'four' }
        case 4:
          return { color: '#FFFF00', key: 'five' }
        case 5:
          return { color: '#FFC0CB', key: 'six' }
      }
    },
    getTitle (type) {
      switch (type) {
        case '':
          return ''
        case 'V':
          return ''
        case 'mbar':
          return ''
        case 'A':
          return ''
        case 'mg/s':
          return ''
        case 'kw':
          return ''
      }
    },
    getValue (type) {
      switch (type) {
        case '':
          return 'temperature'
        case 'V':
          return 'voltage'
        case 'mbar':
          return 'pressure'
        case 'A':
          return 'current'
        case 'mg/s':
          return 'flow'
        case 'kw':
          return 'power'
      }
    },
    clearLayout () {
      this.layout = []
      this.infoData = {}
      this.ids = []
      this.layoutStore = []
    },
    changeTab (type) {
      this.tab = type
      this.changeScreen(1)
    },
    addChart (e) {
    }
  }
}
</script>

<style lang="less" scoped>
.flow{
  display: flex;
  flex-wrap: wrap;
}
.tab{
  width: 100%;
  display: flex;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  margin-bottom: 31px;
  position: relative;
  .btn{
    color: #fff;
    font-size: 14px;
    font-family: Alibaba PuHuiTi;
    font-weight: 400;
    line-height: 32px;
    padding: 4px 0;
    text-align: center;
    width: 80px;
    border-radius: 8px 8px 0 0;
    &:first-child{
      margin-right: 10px;
    }
  }
  .download{
    position: absolute;
    right: 18px;
  }
}
.common-flex {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: #21232E;
  .changeView{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 9px 0;
    height: 100%;
    justify-content: center;
    background-color: #21232E;
  }
  .grid{
    display: flex;
      height: 100%;
      width: 1386px;
      background-color: #19181D;
      padding: 12px 18px 6px 6px;
      .grid-item{
        background-color: #fff;
      }
  }
  .changeBtn{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 6px 0;
    height: 100%;
    justify-content: center;
    background-color: #21232E;
  }
}
.moveBox{
  display: flex;
}
.detail-target {
  width: 100%;
  height: 100%;
  padding: 20px 24px;
  overflow: scroll;
}
</style>



