
<template>
  <div class="detail-target">
    <iframe :src="suanpanLink" style="width:102.5%;height:100%;" frameborder="0"></iframe>
  </div>
</template>

<script>
import { getTwin } from '@/api/device'

export default {
  name: 'orignal-data',
  components: {},
  data () {
    return {
      suanpanLink: ''
    }
  },
  mounted () {
    this.getTwin()
  },
  methods: {
    getTwin () {
      getTwin().then(res => {
        if (res.code === 200) {
          this.suanpanLink = res.data
        } else {
          this.$Message.warning(res.msg)
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.detail-target{
  height: 100%;
  overflow: hidden;
}
</style>



