<template>
  <div class="graph-viewer" ref="viewer" :style="{background: graphStyle.background}">
    <div class="graph" ref="graph" :style="graphStyle" v-show="showView"></div>
    <!--<Icon type="md-refresh" class="btn-reset" v-show="showModuleId" @click="reset()" style="color:white;"/>-->
    <Icon type="md-backspace" size="26" class="btn-reset" v-show="showModuleId.length>0" @click="reset()"/>
    <!--<Button type="default" ></Button>-->
    <div class="graph-cover" v-show="!showView && !LoadComplete">
      <!-- {{LoadComplete?'':'Loading...'}} -->
      <Icon type="ios-loading" style="animation: ani-demo-spin 1s linear infinite;"/>
      <!--      <CommonLoading class="loading" type="debuounce" :size="{width:50,height:50}"-->
      <!--                     :options="{color:'rgba(25,255,255,.7)'}"></CommonLoading>-->
    </div>
    <Modal :title="$t_p('issue_order')" class="plan-modal"
           footer-hide=""
           v-model="showEdit"
           :mask-closable="false"
           class-name="vertical-center-modal"
           :width="$flexible(400)"
    >
      <div class="plan-edit" v-if="showEdit">
        <Form class="edit-form" ref="formBase" :rules="InfoRules" :model="currentEdit"
              :label-width="$flexible(110)">
          <FormItem class="form-item" :label="`${$t_p('issue_param')}`" prop="ruleName">
            <Input :placeholder="$t_p('issue_param_placeholder')" v-model="currentEdit.args"/>
          </FormItem>
          <div class="form-ops " style="text-align: right;padding-bottom: 0">
            <Button class="btn btn-cancel" :disabled="planSaving" @click="showEdit=false">{{$t_p('cancel')}}</Button>
            <Button class="btn" type="primary" :loading="planSaving" @click="onPlanSave">{{$t_p('issue')}}</Button>
          </div>
        </Form>
      </div>
    </Modal>
    <Modal :title="$t_p('history_data')" class="plan-modal"
           footer-hide=""
           v-model="showInfo"
           :mask-closable="false"
           class-name="vertical-center-modal"
           :width="$flexible(500)"
    >
      <div class="plan-edit" v-if="showInfo">
        <DatePicker
          v-show="false"
          v-model="timeRange"
          format="yyyy-MM-dd HH:mm:ss"
          type="datetimerange"
          placement="bottom-end"
          :placeholder="$t_p('range_time_placeholder')"
          style="width: 350px"
        ></DatePicker>
        <div class="chart" style="width: 100%;height: 400px">
          <Spin fix v-show="chartLoading" style="width: 100%;height: 100%">
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div>Loading</div>
          </Spin>
          <ChartBase
            style="width: 100%;height: 100%"
            class="chart-ele"
            ref="chart"
            :options="chartOptions"
          ></ChartBase>
        </div>
      </div>
    </Modal>
    <!--    <div class="tip" :style="tipStyle">-->
    <!--      <div class="title">-->
    <!--        <div></div>-->
    <!--        <div class="close" @click="hideTip()"></div>-->
    <!--      </div>-->
    <!--      <div class="chart" style="width: 100%;height: 400px">-->
    <!--        <Spin fix v-show="chartLoading" style="width: 100%;height: 100%">-->
    <!--          <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>-->
    <!--          <div>Loading</div>-->
    <!--        </Spin>-->
    <!--        <ChartBase-->
    <!--          style="width: 100%;height: 100%"-->
    <!--          class="chart-ele"-->
    <!--          ref="chart"-->
    <!--          v-show="!chartLoading"-->
    <!--          :options="chartOptions"-->
    <!--        ></ChartBase>-->
    <!--      </div>-->
    <!--    </div>-->
  </div>
</template>

<script>

// import {Graph} from './Graph'
// import {mxUtils,mxCellTracker,mxCodec} from 'mxgraph-js'
import CommonLoading from '@/components/common-loading'
import echarts from 'echarts'
import ChartBase from '@/components/chart-base'
import {editCmd, getBindDataList, getModuleInfo} from '../../api/api'
import {getDeviceItemsHistoryData} from '../../api/device'

import {cellToInfo, getCellBindType, mappingDataToCell} from '@/libs/util'
import moment from 'moment'

export default {
  name: 'graph-viewer',
  i18n_prefix: 'component.graph-viewer',
  components: {
    CommonLoading, ChartBase
  },
  props: {
    // ["moduleId", "background"],
    moduleId: {
      type: [String, Number]
    },
    background: {
      type: String
    },
    zoomEnable: {
      type: Boolean,
      default () {
        return false
      }
    },
    panEnable: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      LoadComplete: false,
      timeRange: [
        moment()
          .add(-1, 'd')
          .format('YYYY-MM-DD HH:mm:ss'),
        moment().format('YYYY-MM-DD HH:mm:ss')
      ],
      showInfo: false,
      tipPosition: null,
      showEdit: false,
      showView: false,
      planSaving: false,
      viewer: null,
      _fla: false,

      flag: 0,
      InfoRules: {},
      currentEdit: {},
      currentModuleInfo: {},
      currentGraphInfo: null,
      scale: 1,
      itemRefreshTimer: null,
      showModuleId: [],
      chartLoading: false,
      chartOptions: {},
      historyData: []
    }
  },
  computed: {
    tipStyle () {
      if (this.tipPosition) {
        return {
          display: 'block',
          left: this.tipPosition[0] + 'px',
          top: this.tipPosition[1] + 'px'
        }
      } else {
        return {
          display: 'none'
        }
      }
    },
    graphStyle () {
      return this.currentGraphInfo ? {
        width: 'auto !important',
        // height:"auto !important",
        // width: (this.currentGraphInfo._pageWidth || this.currentGraphInfo.pageWidth) * this.scale + 'px',
        // height: (this.currentGraphInfo._pageHeight || this.currentGraphInfo.pageHeight) * this.scale + 'px',
        // background: this.background || this.currentGraphInfo.background
        background: 'black'
      } : {
        width: 'auto !important'
        //  height:"auto !important",
      }
    }
  },
  watch: {
    showModuleId (v) {
      if (v && v.length) {
        this.loadModuleInfo(v[v.length - 1])
      }
    },
    moduleId (v) {
      this.showModuleId = []
      this.loadModuleInfo(v)
    }
  },
  methods: {
    showTip (x, y) {
      this.tipPosition = [x + 10, y]
    },
    hideTip () {
      this.tipPosition = null
    },
    initViewer () {
      return new Promise((resolve, reject) => {
        mxUtils.get('./static/vendor/mxgraph/default.xml', (xhr) => {
          var themes = new Object()
          themes[Graph.prototype.defaultThemeName] = xhr.getDocumentElement()
          var graph = this.viewer = new Graph(this.$refs.graph, undefined, undefined, undefined, themes)
          graph.resizeContainer = true
          graph.setEnabled(false)

          function updateEdgeStyle (graph, edge) {
            // setTimeout(() => {
            try {
              if (edge.getStyle().includes('shape=flowLine')) {
                let ele = graph.view.getState(edge).shape.node
                ele.getElementsByTagName('path')[0].removeAttribute('visibility')
                ele.getElementsByTagName('path')[0].setAttribute('stroke-width', '6')
                ele.getElementsByTagName('path')[0].setAttribute('stroke', 'white')
                ele.getElementsByTagName('path')[1].setAttribute('class', 'flow')
              }
            } catch (e) {
            }
            // }, 10)
          }

          graph.addListener(mxEvent.REFRESH, function (sender, evt) {
            // debugger;
            sender.view.states.visit(function (key, state) {
              if (sender.model.isEdge(state.cell)) {
                updateEdgeStyle(sender, state.cell)
              }
            })
          })
          graph.addListener(mxEvent.SIZE, function (sender, evt) {
            // debugger;
            sender.view.states.visit(function (key, state) {
              if (sender.model.isEdge(state.cell)) {
                updateEdgeStyle(sender, state.cell)
              }
            })
          })

          graph.addListener(mxEvent.CELLS_ADDED, function (sender, evt) {
            // debugger;
            let edge = evt.properties.cells[0]
            if (edge && edge.isEdge()) {
              updateEdgeStyle(sender, edge)
            }
          })

          if (this.zoomEnable) {
            this.$refs.graph.addEventListener('mousewheel', function (evt) {
              evt.preventDefault()
              evt.stopPropagation()
              if (evt.wheelDelta > 100) {
                let zoom = 1.1
                graph.zoom(zoom)
              } else if (evt.wheelDelta < -100) {
                let zoom = 0.9
                graph.zoom(zoom)
              }
            }, {
              passive: false
            })
          }

          if (this.panEnable) {
            let _isMoving = false, _startPoint
            this.$refs.graph.addEventListener('mousedown', function (evt) {
              _isMoving = true
              _startPoint = evt
            })
            this.$refs.graph.addEventListener('mousemove', function (e) {
              if (_isMoving) {
                if (!_startPoint) {

                } else {
                  let deltaX = e.clientX - _startPoint.clientX
                  let deltaY = e.clientY - _startPoint.clientY
                  graph.panGraph(deltaX, deltaY)
                }
              }
            })
            this.$refs.graph.addEventListener('mouseup', function (evt) {
              _isMoving = false
              _startPoint = null
            })
            this.$refs.graph.addEventListener('mouseleave', function (evt) {
              _isMoving = false
              _startPoint = null
            })
          }

          // window.onresize = this.refreshScale
          resolve(graph)
        }, () => {
          reject()
        })
      })
    },
    onPlanSave () {
      this.editCmd(this.currentEdit)
      this.showEdit = false
      // console.log(this.editCmd(this.this.editCmd(this.currentEdit)))
    },
    updateGraphInfo (xmlstring = '') {
      const xmlDoc = mxUtils.parseXml(xmlstring)
      let res = /pageWidth="(\d+)".*pageHeight="(\d+)"/.exec(xmlstring) || []
      let pageSize
      if (res[1] && res[2]) {
        pageSize = {
          pageWidth: res[1] * 1,
          pageHeight: res[2] * 1
        }
      }
      const codec = new mxCodec(xmlDoc)
      this.currentGraphInfo = codec.decode(xmlDoc.documentElement, this.viewer.getModel())
      this.refreshScale(pageSize).then(() => {
        if (pageSize) {
          this.showView = true
        } else {
          this.showView = false
        }
      })
    },
    loadModuleInfo (moduleId) {
      if (!moduleId) {
        if (this.showModuleId.length) {
          moduleId = this.showModuleId[this.showModuleId.length - 1]
        } else {
          moduleId = this.moduleId
        }
      }
      if (moduleId) {
        this.showView = false
        getModuleInfo({
          id: moduleId
        }).then((res) => {
          this.currentModuleInfo = res.data || {}
          this.updateGraphInfo(this.currentModuleInfo.xmlstring || '')
          if (this.currentModuleInfo.type === 1) {
            this.startGraphRefresh()
          } else {
            this.stopGraphRefresh()
          }
          this.LoadComplete = true
        }, (e) => {
          this.$Message.error(this.$t_p('get_fail'))
        })
      }
    },
    refreshScale (graphSize) {
      return new Promise((resolve, reject) => {
        clearTimeout(this.refreshTimer)
        this.refreshTimer = setTimeout(() => {
          if (!graphSize) {
            graphSize = this.currentGraphInfo
          }
          let viewEl = this.$refs && this.$refs.viewer
          if (graphSize && graphSize.pageWidth && graphSize.pageHeight && viewEl) {
            let pw = viewEl.clientWidth, ph = viewEl.clientHeight
            let gw = Math.abs(this.currentGraphInfo._pageWidth || this.viewer.view.graphBounds.width + this.viewer.view.graphBounds.x * 2)
            let gh = Math.abs(this.currentGraphInfo._pageHeight || this.viewer.view.graphBounds.height + this.viewer.view.graphBounds.y * 2)

            let scale = 1
            if (pw / ph > gw / gh) {
              scale = ph / gh
            } else {
              scale = pw / gw
            }
            this.currentGraphInfo._pageWidth = gw
            this.currentGraphInfo._pageHeight = gh

            this.viewer.zoomTo(scale, false)
            this.scale = scale
            resolve()
          } else {
            reject && reject()
          }
        }, 10)
      }).catch(error => {})
    },

    startGraphRefresh () {
      this.stopGraphRefresh()
      this.refreshGraphDataView()
      if (this.currentModuleInfo.refreshInterval) {
        // let time = this.currentModuleInfo.refreshInterval * 1000
        let time = localStorage.getItem('freshTime') ? Number(localStorage.getItem('freshTime')) * 1000 : 5000
        this.itemRefreshTimer = setInterval(() => {
          this.refreshGraphDataView()
          // console.log('refreshGraphDataView')
        }, time)
      }
    },
    stopGraphRefresh () {
      clearInterval(this.itemRefreshTimer)
    },
    getItemId (info) {
      let name = info.detail.itemName
      if (name) {
        let val = name.split('(')
        if (val) {
          let temp = val[1].split(')')
          if (temp) {
            return temp[0]
          }
        }
      }
      return 0
    },
    refreshGraphDataView () {
      let graph = this.viewer
      let model = graph.getModel()
      let items = []
      let itemCellMap = {}

      for (let i in model.cells) {
        let info = cellToInfo(model.cells[i], {
          graph
        })

        if (info.detail.type === '1') {
          let key = `${info.detail.deviceId}-${info.detail.item}`
          if (itemCellMap[key]) {
            itemCellMap[key].push(model.cells[i])
          } else {
            let itemId = this.getItemId(info)
            itemCellMap[key] = [model.cells[i]]
            items.push({
              itemId,
              deviceId: info.detail.deviceId
            })
          }
        }
      }
      items.length && getBindDataList({
        items: items
      }).then((res) => {
        (res.data || []).forEach((d) => {
          let key = `${d.deviceId}-${d.itemId}`
          if (itemCellMap[key]) {
            itemCellMap[key].forEach((cell) => {
              let info = cellToInfo(cell, {
                graph
              })

              const needBindText = ['true', '1'].includes(info.detail.bindText + '')
              cell.setAttribute('xl-bind-itemVal', d.val)
              if (needBindText) {
                let val = d.val == null ? '' : d.val + ''
                if (val) {
                  val = Number(val)
                  const {bindTextOp = '*', bindTextOpValue = 1} = info.detail || {}
                  let opValue = Number(bindTextOpValue)
                  switch (bindTextOp) {
                    case '*':
                      val = val * opValue
                      break
                    case '/':
                      val = val / opValue
                      break
                    case '+':
                      val = val + opValue
                      break
                    case '-':
                      val = val - opValue
                      break
                  }
                  if (info.valueFixed) {
                    val = val.toFixed(info.valueFixed)
                  }
                }
                cell.setAttribute('label', val)
              }
              if (info.detail.mapping && info.detail.mapping.length) {
                let newData = info.detail.mapping.map(item => {
                  return {
                    ...item,
                    x1: item.x1 ? item.x1 : (item.x1 === 0 ? item.x1 : 1)
                  }
                })
                mappingDataToCell(cell, newData, d)
              }
            })
          }
        })
        graph.refresh()
      })
    },

    reload () {
      this.loadModuleInfo()
    },
    reset () {
      let nextList = [].concat(this.showModuleId)
      nextList.pop()
      this.showModuleId = nextList
      if (nextList.length === 0) {
        this.loadModuleInfo(this.moduleId)
      }
    },
    editCmd (data) {
      editCmd(data).then(res => {
        // console.log(123,res)
        this.$Message.success(res.data)
      }, res => {
        if (res.code == 1000) {
          this.$Message.error(res.msg)
        } else {
          this.$Message.error(this.$t_p('order_issue_fail'))
        }
      })
    },
    getChartSeries (yData, name) {
      return {
        name: name,
        type: 'line',
        // smooth: true,
        showSymbol: false,
        lineStyle: {
          normal: {
            color: '#00c1de'
          }
        },
        itemStyle: {
          show: false,
          normal: {
            color: '#00c1de'
          }
        },
        areaStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: '#f8fafa'
              },
              {
                offset: 1,
                color: '#ecf7f8'
              }
            ])
          }
        },
        data: yData
      }
    },
    getChartGrid (yData) {
      let max = 0
      yData.forEach(d => {
        max = Math.max(max, Math.abs(d.value[1]))
      })

      max = max.toFixed(0).length * 12 + 30
      return {
        // show:true,
        left: max, // "8%",
        right: '5%',
        top: 30,
        bottom: 80
      }
    },
    loadDeviceItemsHistoryData (deviceId, itemId, name) {
      this.chartLoading = true
      let stime = this.timeRange[0]
      let etime = this.timeRange[1]

      if (stime) {
        stime = moment(stime).format('YYYY-MM-DD HH:mm:ss')
      }
      if (etime) {
        etime = moment(etime).format('YYYY-MM-DD HH:mm:ss')
      }
      let info = {
        deviceId: deviceId,
        itemId: itemId,
        sDate: stime,
        eDate: etime
        // pageNumber: this.planPageInfo.pageIndex,
        // pageSize: this.planPageInfo.pageSize
      }

      getDeviceItemsHistoryData({
        ...info
      })
        .then(res => {
          this.historyData = (res.data.result || []).map(d => ({
            ...d,
            dateTime: d.dateTime || d.dataTime
          }))
          this.chartLoading = false
          // this.historyData1 = this.historyData.slice(0,9);

          // this.planPageInfo.totalElements = this.historyData.length/10 || 0;
          // this.planPageInfo.totalElements = this.historyData.length || 0;

          // console.log(111,this.planPageInfo)

          this.refreshChart(name)
        })
    },
    refreshChart (name) {
      let yData = []

      let chartData = [].concat(this.historyData).reverse()
      chartData.forEach(d => {
        let time = moment(d.dateTime)
        yData.push({
          name: time.toDate(),
          value: [time.toDate(), Number(d.val || 0)]
        })
      })

      this.chartOptions = {
        calculable: false,
        animation: false,

        tooltip: {
          trigger: 'axis',
          position: function (pt) {
            return [pt[0], '10%']
          }
        },
        title: {
          left: 'center',
          text: ''
        },
        grid: this.getChartGrid(yData),
        xAxis: {
          type: 'time',
          // boundaryGap: false,
          axisTick: {
            show: false,
            lineStyle: {
              color: '#00c1de'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#878787'
            }
          },
          splitLine: {
            show: false
          }
          // data: xData
        },
        yAxis: {
          type: 'value',
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#878787'
            }
          },
          splitLine: {
            show: false
          }
        },
        // dataZoom: [
        //   {
        //     type: "slider"
        //   }
        // ],
        series: [this.getChartSeries(yData, name)]
      }

      // this.$refs.chart.instance.resize();
    }
  },
  mounted () {
    this.initViewer().then((view) => {
      // 
      let track = new mxCellTracker(view)
      track.mouseMove = function (sender, e) {
        let cell = e.sourceState && e.sourceState.cell
        if (cell) {
          let type = getCellBindType(cell)
          let info = cellToInfo(cell, {graph: view})
          if (type == 2) {
            e.getState().setCursor('pointer')
          }
          if (info.detail && info.detail.cmdType == 4) {
            let infoName = info.detail.infoName ? info.detail.infoName : ''
            if (infoName.indexOf('group') != -1) {
              e.getState().setCursor('pointer')
            }
            if (infoName.indexOf('item') != -1) {
              e.getState().setCursor('pointer')
            }
          }
        }
      }

      view.getTooltipForCell = (cell) => {
        let type = getCellBindType(cell)
        let info = cellToInfo(cell, {graph: this.viewer})
        if (type == 2) {
          return ` ${info.detail.moduleName}`
        }
        if (info.detail && info.detail.cmdType == 4) {
          let infoName = info.detail.infoName ? info.detail.infoName : ''
          if (infoName.indexOf('group') != -1) {
            return ``
          }
          if (infoName.indexOf('item') != -1) {
            return ``
          }
        }
      }

      // 

      view.click = (e) => {
        if (!this.$hasAccess('dev.edit')) {
          return
        }
        let cell = e.sourceState && e.sourceState.cell
        if (cell) {
          let info = cellToInfo(cell, {graph: view})
          if (info.detail && info.detail.cmdType == 0 || info.detail.cmdType == 1) {
            let obj = {}
            obj.args = info.detail.cmdType
            obj.devices = info.detail.devices
            obj.bindTime = info.detail.bindTime || 10
            this.currentEdit = {...obj}
            this.editCmd(this.currentEdit)
          }
          if (info.detail && info.detail.cmdType == 2) {
            let obj = {}
            obj.devices = info.detail.devices
            obj.bindTime = info.detail.bindTime || 10
            this.currentEdit = {...obj}
            this.showEdit = true
          }
          if (info.detail && info.detail.cmdType == 3) {
            let obj = {}
            obj.args = ''
            obj.devices = info.detail.devices
            obj.bindTime = info.detail.bindTime || 10
            this.currentEdit = {...obj}
            this.editCmd(this.currentEdit)
          }
          // 
          if (info.detail && info.detail.cmdType == 4) {
            let infoName = info.detail.infoName ? info.detail.infoName : ''
            // if(infoName.indexOf("video"))

            if (infoName.indexOf('item') != -1) {
              let infos = info.detail || {}
              let deviceInfo = {
                deviceId: infos.deviceId,
                id: infos.device,
                itemVal: infos.itemVal,
                itemId: infos.item,
                itemName: infos.itemName
              }
              this.$eventHub.$emit('deviceInfo', JSON.stringify(deviceInfo))
            }
            if (infoName.indexOf('alarm') != -1) {
              let infos = info.detail || {}
              let alarmInfo = {
                deviceId: infos.deviceId,
                id: infos.device,
                itemVal: infos.itemVal,
                itemId: infos.item,
                itemName: infos.itemName
              }
              this.$eventHub.$emit('alarmInfo', JSON.stringify(alarmInfo))
            }
            if (infoName.indexOf('video') != -1) {
              let videoId = +infoName.substring(5)
              this.$eventHub.$emit('videoId', videoId)
            }
            if (infoName.indexOf('group') != -1) {
              let groupId = +infoName.substring(5)
              this.$eventHub.$emit('groupId', groupId)
            }
          }

          if (info.detail && info.detail.infoType == 1) {
            let infoDetail = {}
            infoDetail.infoDevice = info.detail.infoDevice
            infoDetail.infoDeviceId = info.detail.infoDeviceId
            infoDetail.infoDeviceName = info.detail.infoDeviceName
            infoDetail.infoItem = info.detail.infoItem
            infoDetail.infoItemName = info.detail.infoItemName
            infoDetail.infoName = info.detail.infoName
            this.$emit('on-info', infoDetail)
          }
          if (info.detail && info.detail.type == 2) {
            this.showModuleId.push(info.detail.module)
          }
        }
      }
    })
    this.loadModuleInfo()
    this.$eventHub.$on('sl-main-size-change', () => {
      this.refreshScale()
    })
  },
  destroyed () {
    this.stopGraphRefresh()
  }
}
</script>

<style>
  .flow {
    stroke-dasharray: 8;
    animation: dash 0.5s linear;
    animation-iteration-count: infinite;
  }

  @keyframes dash {
    to {
      stroke-dashoffset: -16;
    }
  }
</style>

<style lang="less" scoped>
  .graph-viewer {
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    .tip {
      position: absolute;
      background: yellow;
      width: 400px;
      height: 300px;
      z-index: 99;
      /* background: @color-blue-light;*/
      /*border-radius: 5px;*/
      color: #000;
      transition: all ease 400ms;
      padding: 10px;
      /*background-size: 100% 100%;*/

      .title {
        height: 30px;
        line-height: 30px;
        display: flex;
        border-bottom: 1px solid #d2d2d2;

        div:first-child {
          flex: 1;
          font-size: 14px;
        }

        .close {
          width: 20px;
          height: 20px;
          font-size: 18px;
          border: 1px solid #d2d2d2;
          line-height: 20px;
          text-align: center;
        }

        .close:hover {
          cursor: pointer;
        }
      }
    }

    .graph {
      margin: 0 auto;
      width: 100% !important;
      /*box-sizing: border-box;*/
      /*border: 1px solid white;*/
    }

    .chart {
      height: 400px;
      .chart-ele {
        width: 100%;
        height: 100%;
      }
    }

    .btn-reset {
      position: absolute;
      left: 10px;
      top: 10px;
      color: white;
      cursor: pointer;
    }

    .graph-cover {
      position: absolute;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      font-size: 24px;
      background-color: #000;
      .loading {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 200px;
        text-align: center;
        margin-left: -100px;
        transform: translateY(-50%);

      }
    }

  }

</style>



