<template>
  <div class="chart"
       :id="`chart-${id}`"></div>
</template>

<script>
export default {
  name: 'chart',
  props: {
    id: {
      type: Number
    },
    width: {
      type: [String, Number],
      require: true,
      default: '100%'
    },
    height: {
      type: [String, Number],
      require: true,
      default: '20%'
    },
    legend: {
      type: Array,
      default: () => {
        return []
      }
    },
    xAxis: {
      type: Object,
      default: () => {
        return {}
      }
    },
    yAxis: {
      type: Object,
      default: () => {
        return {}
      }
    },
    data: {
      type: Array,
      default: () => {
        return []
      }
    },
    type: {
      type: String,
      default: () => {
        return ''
      }
    }
  },
  data () {
    return {
      xAxisData: []
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let chartDom = document.getElementById(`chart-${this.id}`)
      let chart = this.$echarts.init(chartDom)
      chart.setOption(this.options)
      chart.on('mouseover', (e) => {
        this.mouseover(e, chart)
      })
      chart.on('mouseout', (e) => {
        this.mouseout(e, chart)
      })
      this.$emit('init', true)
    },
    mouseover (e, chart) {
      this.xAxisData = this.options.xAxis.data
      let data = this.options.xAxis.data.map(v => {
        if (v === e.name) {
          v = {
            value: e.name,
            textStyle: {
              fontWeight: 'bold',
              color: '#fff'
            }
          }
          return v
        }
        return v
      })
      chart.setOption({
        xAxis: {
          data
        }
      })
    },
    mouseout (e, chart) {
      chart.setOption({
        xAxis: {
          data: this.xAxisData
        }
      })
    },
    dataType (data) {
      return Object.prototype.toString.call(data)
    }
  },
  computed: {
    options () {
      let options = {
        xAxis: {
          type: 'category',
          data: this.data.map((v) => v[this.xAxis.key]),
          axisLine: {
            lineStyle: {
              // color: 'rgba(0, 0, 0, 0.050980392156862744)',
              color: 'rgba(255,255,255,0.6)'
            }
          },
          axisLabel: {
            // color: 'rgba(22,22,22,.4)',
            color: 'rgba(255,255,255,0.6)'
          },
          axisTick: {
            show: false
          }
        },
        position: function (point, params, dom, rect, size) {
          // 
          let x = 0 // x
          // 
          let pointX = point[0]
          // 
          let boxWidth = size.contentSize[0]
          // boxWidth > pointX 
          if (boxWidth <= pointX) {
            x = pointX - boxWidth
          } else {
            x = pointX
          }
          return [x, 50]
        },
        tooltip: {
          trigger: 'axis'
          // backgroundColor:'transparent',
        },
        yAxis: {
          type: 'value',
          name: this.yAxis && this.yAxis.symbol,
          nameTextStyle: {
            // color: 'rgba(22,22,22,.4)',
            color: 'rgba(255,255,255,0.6)',
            padding: [0, 0, -5, -20]
          },
          splitNumber: 2,
          axisLine: {
            lineStyle: {
              // color: 'rgba(0, 0, 0, 0.050980392156862744)',
              color: 'rgba(255,255,255,0.6)'
            }
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            // color: 'rgba(22,22,22,.4)',
            color: 'rgba(255,255,255,0.6)'
          },
          splitLine: {
            lineStyle: {
              type: 'dashed',
              // color: 'rgba(0, 0, 0, 0.0784313725490196)',
              color: 'rgba(255,255,255,0.0784313725490196)'
            }
          }
        },
        grid: {
          top: '50',
          bottom: '15%',
          left: '47',
          right: '20'
          // containLabel: true
        },
        legend:
          this.legend.length > 1
            ? {
              data: this.legend.map((v) => v.name),
              icon: 'circle',
              itemWidth: 7,
              itemHeight: 7,
              right: 0,
              textStyle: {
                color: '#cccc'
              }
            }
            : {
              show: false
            },
        series: this.legend.map((v) => {
          if (this.type === 'line') {
            return {
              name: v.name,
              type: this.type,
              data: this.data.map((val) => val[v.key]),
              smooth: true,
              itemStyle: {
                color: v.color
              },
              lineStyle: {
                color: v.color,
                shadowBlur: 6,
                shadowColor: 'rgba(0, 0, 0, 0.16)',
                shadowOffsetY: 3
              },
              toolTip: {
                show: true
              },
              emphasis: {
                label: {
                  show: true,
                  position: 'outside'
                }
              }
            }
          } else if (this.type === 'bar') {
            return {
              name: v.name,
              type: this.type,
              barWidth: 8,
              barGap: 0,
              showBackground: true,
              data: this.data.map((val) => val[v.key]),
              smooth: true,
              itemStyle: {
                color: v.color
              },
              backgroundStyle: {
                color: '#F0F3F8'
              },
              emphasis: {
                label: {
                  show: true,
                  position: 'outside'
                }
              }
            }
          }
        })
      }
      return options
    }
  }
}
</script>

<style lang="less" scoped>
.chart {
  width: 100%;
  height: 100%;
}
</style>



